.card {
  background: rgb(255, 255, 255);
  border-radius: 2px;
  height: 20%;
  margin: 10px;
  position: relative;
  width: 20%;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
  overflow: hidden;
  text-align: center;
  background: #6caddc;
}

.card > .img-container > img {
  width: 100%;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}
